import React, {useState} from 'react';
import { CarouselProvider, Slider, Slide } from 'pure-react-carousel';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'pure-react-carousel/dist/react-carousel.es.css';
import "./subcatCarousel.css"

const SubcatCarousel = ({ subcategories, setActiveSubcategory }) => {
  
  return (
    <div className="scarousel">
      <CarouselProvider
        className="scarousel-provider"
        isIntrinsicHeight={true}
        totalSlides={!!subcategories && subcategories.categories.length}
        visibleSlides={3.25}
        currentSlide={0}
        touchEnabled={true}
      >
        <div className="scarousel-container">
          <Slider className="sslider" classNameTray="stray" classNameTrayWrap="stray-wrap">
            {!!subcategories && subcategories.categories.map((subcat, idx) => (
              <Slide index={idx} key={idx}>
                <div className="sslide-container">
                  <div 
                    className="sslide"
                    id={`sslide-${idx}`}
                    onClick={() => setActiveSubcategory(`${idx}`)}
                  >
                    {subcat.name}
                  </div>
                </div>
              </Slide>
            ))}
          </Slider>

        </div>
      </CarouselProvider>
    </div>
  );
}

export default SubcatCarousel;

