import React from 'react';
import { CarouselProvider, Slider, Slide, Image } from 'pure-react-carousel';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'pure-react-carousel/dist/react-carousel.es.css';
import "./productCarousel.css"

const ProductCarousel = ({balloons, setActiveBalloon}) => {

  return (
      <div className="productCarousel">
      <CarouselProvider
        className="carousel-provider"
        isIntrinsicHeight={true}
        totalSlides={!!balloons && balloons.length}
        visibleSlides={2.25}
        // currentSlide={0}
      >
        <Slider className="slider" classNameTray="tray" classNameTrayWrap="tray-wrap">
          {!!balloons && balloons.map((balloon, idx) => (
            <Slide index={idx} key={idx} className='product-slide'>
              <div 
                className="slide-container" 
                id={`active-product-${idx}`}
                onClick={() => setActiveBalloon(idx)}
              >
                <div className="image">
                  <img className="p-image" src={`https://partycity.scene7.com/is/image/PartyCity/_sq_?$_500x500_$&$product=PartyCity/${balloon.represented_product.id}`} />
                </div>
                <div className="name">
                  {balloon.product_name.toUpperCase()}
                </div>
                <div className="price">
                  ${balloon.price}
                </div>
                <div className="add" id={`add-${idx}`}>
                  <button className="button" onClick={() => window.CloningBalloon(`${balloon.represented_product.id}`)}>
                    ADD
                  </button>
                </div>  
              </div>
            </Slide>
          ))}
        </Slider>
      </CarouselProvider>
    </div>
  );
}

export default ProductCarousel;

